//variables declarations 

let x=[];
let y=[];
let r=40;
let asteroids=[];
let vibracionAnillo=1;
//setup
function setup(){
    createCanvas(windowWidth,windowHeight);
    for(let i=0;i<10;i++){
        asteroids.push(new asteroid());
    }
}
function windowResized() {
    resizeCanvas(windowWidth, windowHeight);
  }
//main loop 
function draw(){
    background(31, 29, 46);
    stroke(255);
    strokeWeight(1);
    noFill();
    for(let j=0;j<asteroids.length;j++){
        asteroids[j].render();
        asteroids[j].update();
        asteroids[j].edges();

    }
    
    //ellipse(width/4,height/4,width/4);//sol
    push();
    translate(width/4,height/4);
    beginShape();
    for(let k=0;k<50;k++){
        let angle=map(k,0,50,0,TWO_PI);
        let x=width/7*cos(angle)+random(0,3);
        let y=width/7*sin(angle)+ random(0,3);
        vertex(x,y);
    }
    endShape(CLOSE);
    pop();
    
    ellipse(width/1.1,height/7,width/9);//planet-b
    ellipse(width/1.2,height/1.5,width/5);//planet-A
    //anillos
    ellipse(width/1.2,height/1.5,width/2.5,width/12);
    // ellipse(width/1.2,height/1.5,width/2,width/10);
    translate(width/1.2,height/1.5);
    push();
    beginShape();
    for(let k=0;k<50;k++){
        let angle=map(k,0,50,0,TWO_PI);
        let x=width/4*cos(angle)+random(0,2);
        let y=width/12*sin(angle)+ random(0,2);
        vertex(x,y);
    }
    endShape(CLOSE);
    pop();
    //planeta-b corteza 
    push();
        beginShape();
        for(let i=0;i<12;i++){
            let angle=map(i,0,12,0,TWO_PI);
            let r=(width/16)+ 2^i;
            let x =r*cos(angle);
            let y=r*sin(angle);
            vertex(x,y);
        }
        endShape(CLOSE);
    pop();

    
}
function asteroid(position,r){
    if (position) {
        this.position = position.copy();
      } else {
        this.position = createVector(random(width), random(height))
      }
      if (r) {
        this.r = r * 0.5;
      } else {
        this.r = random(15, 50);
      }
    this.position=createVector(random(width),random(height));//se asigan un vector a la posiscion del asteroide
    this.r=random(30,60);//se crea un radio aleatorio para el circulo o en este caso asteroide
    this.vel=p5.Vector.random2D();// direccion de la velocidad del asteroide se asgigna un vector aleatorio de -1 a 1
    this.total=round(random(5,12));//el total de vertices del asteroide
    this.offset=[];//arreglo de desfasamientos del radio del asteroide respecto a los vertices
    for(i=0;i<this.total;i++){
        this.offset[i]=random(-this.r*0.5,this.r*0.5);
    }   
    this.update=function(){
        this.position.add(this.vel);
    }
    this.render=function(){
        push();
        stroke(255);
        noFill();
        translate(this.position.x,this.position.y);
        beginShape();
        for(let i=0;i<this.total;i++){
            let angle=map(i,0,this.total,0,TWO_PI);//convierte cada punto o vertice en un valor representativo en escala radianes (ej.0-5 en angulos 2pi/5 para 0 hasta 2pi para 5)
            let r=this.r+this.offset[i];
            let x =r*cos(angle);
            let y=r*sin(angle);
            vertex(x,y);
        }
        endShape(CLOSE);
        pop();
    }
    this.edges=function(){
        if(this.position.x>width+this.r){//cuando atraviesa el borde derecho
            this.position.x=-this.r;
        }else if(this.position.x<-this.r){//cuando atraviesa el borde izquierdo
            this.position.x=width+this.r;
        }
        if(this.position.y>height+this.r){//cuando atraviesa el borde superior
            this.position.y=-this.r;
        }else if(this.position.y<-this.r){//cuando atraviesa el borde inferior
              this.position.y=height+this.r;
          }
    }
    
}